:root {
  --highlight: thistle;
}



/*GENERAL*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  
}

body {
  height: 100vh;
  width: 100vw;
  background-color: rgb(179, 244, 255);
}

#root {
  height: 100%;
  max-width: 500px;
  max-height: 800px;
  margin: 0 auto;
  background-color: white;
}
.App{
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 50px auto;
}
/*NAVBAR*/
.navbar {
  display: grid;
  grid-template-columns: 50px auto;
  grid-template-rows: 50px auto;
}
.dropbtn {
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  grid-row: 1 / 2;
  grid-column: 1 / 2;
}

.pageTitle {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  padding-right: 25px;
}

.navbar:hover .dropbtn {background-color: var(--highlight)}

.navbar {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  left: 0;
  top: 50px;
  width: 100%;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #f1f1f1;}
.navbar:hover .dropdown-content {display: block;}

/*VIEW*/
.view {
  background-color: white;
}